// src/utils.js
import React, { useEffect, useState } from "react";

export const days = [
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
  "Sunday",
];

// Helper: ISO week number (1-53)
function getISOWeekNumber(date) {
  const tmp = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()));
  const dayNr = (tmp.getUTCDay() + 6) % 7 + 1; // 1..7 (Mon..Sun)
  tmp.setUTCDate(tmp.getUTCDate() + 4 - dayNr);
  const yearStart = new Date(Date.UTC(tmp.getUTCFullYear(), 0, 1));
  return Math.ceil(((tmp - yearStart) / 86400000 + 1) / 7);
}

// Return an array of Date objects for Monday..Sunday of the "current" week,
// with the Sunday 10:00 AM rule applied.
export const getWeekDates = () => {
  const now = new Date();

  // Sunday after 10am should be considered next week
  if (now.getDay() === 0 && now.getHours() >= 10) {
    now.setDate(now.getDate() + 1); // move to Monday
  }

  // Find Monday of the week
  const day = now.getDay(); // 0=Sun,1=Mon..6=Sat
  const diffToMonday = (day === 0 ? -6 : 1) - day;
  const monday = new Date(now);
  monday.setDate(now.getDate() + diffToMonday);
  monday.setHours(0, 0, 0, 0);

  const dates = [];
  for (let i = 0; i < 7; i++) {
    const d = new Date(monday);
    d.setDate(monday.getDate() + i);
    dates.push(d);
  }
  return dates;
};

// Stable week id used by the app: "YYYY-<ISOweek>"
export const getWeekId = () => {
  const now = new Date();
  if (now.getDay() === 0 && now.getHours() >= 10) {
    now.setDate(now.getDate() + 1);
  }
  const isoWeek = getISOWeekNumber(now);
  const year = now.getFullYear();
  return `${year}-${isoWeek}`;
};

// Small countdown component (used in MealTracker)
export const CountdownToReset = () => {
  const calculateNextReset = () => {
    const now = new Date();
    // get next Sunday 10:00 local time
    const daysUntilSunday = (7 - now.getDay()) % 7;
    let nextSunday = new Date(now);
    nextSunday.setDate(now.getDate() + daysUntilSunday);
    nextSunday.setHours(10, 0, 0, 0);

    if (now.getDay() === 0 && now.getHours() >= 10) {
      nextSunday.setDate(nextSunday.getDate() + 7);
    }
    return nextSunday - now;
  };

  const [timeLeft, setTimeLeft] = useState(calculateNextReset());

  useEffect(() => {
    const t = setInterval(() => setTimeLeft(calculateNextReset()), 1000);
    return () => clearInterval(t);
  }, []);

  if (timeLeft <= 0) return <p>Resetting week...</p>;

  const daysLeft = Math.floor(timeLeft / (1000 * 60 * 60 * 24));
  const hoursLeft = Math.floor((timeLeft / (1000 * 60 * 60)) % 24);
  const minutesLeft = Math.floor((timeLeft / (1000 * 60)) % 60);
  const secondsLeft = Math.floor((timeLeft / 1000) % 60);

  return (
    <p style={{ marginTop: "10px", fontStyle: "italic", color: "#555" }}>
      The app will reset the week at Sunday 10:00 AM (in {daysLeft} day
      {daysLeft !== 1 ? "s" : ""}, {hoursLeft} hour
      {hoursLeft !== 1 ? "s" : ""}, {minutesLeft} minute
      {minutesLeft !== 1 ? "s" : ""} and {secondsLeft} second
      {secondsLeft !== 1 ? "s" : ""}).
    </p>
  );
};



