import React from 'react';

export default function Button({ children, variant = 'primary', className = '', ...props }) {
  const base = 'inline-flex items-center justify-center gap-2 px-4 py-2 rounded-lg font-semibold transition-all duration-200 focus-ring';
  const variants = {
    primary: 'btn-primary',
    danger: 'btn-danger',
    ghost: 'bg-transparent text-textPrimary hover:bg-primary/10',
  };
  const variantClass = variants[variant] || variant;
  return (
    <button className={`${base} ${variantClass} ${className || ''}`} {...props}>
      {children}
    </button>
  );
}
