// src/WelcomePage.js
import React from "react";
import { useNavigate } from "react-router-dom";
import { useUser } from "./UserContext";
import { motion } from "framer-motion";
import Card from "./components/ui/Card";
import Button from "./components/ui/Button";

function WelcomePage() {
  const { login } = useUser(); //  use the exported hook
  const navigate = useNavigate();

  const handleUserClick = (name) => {
    login(name); //  use login instead of setUser
    navigate("/tracker");
  };

  const users = ["Ram", "Vaishu", "Suppiah", "Komathi"];

  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, ease: "easeOut" }}
      className="flex items-center justify-center min-h-screen relative"
    >
      <div className="card w-full max-w-md text-center">
        <h1 className="text-3xl font-extrabold mb-6 text-gray-800">
          Welcome to LunchLogger!
        </h1>
        <p className="text-sm text-muted mb-6">Select your name to continue</p>
        <div className="grid gap-4">
          {users.map((user) => (
            <Button
              key={user}
              onClick={() => handleUserClick(user)}
              className={`btn-primary ${
                user === "Komathi"
                  ? "bg-gradient-to-r from-red-500 via-red-600 to-orange-500"
                  : ""
              }`}
            >
              {user}
            </Button>
          ))}
        </div>
      </div>

      {/*  Footer in bottom-right corner */}
      <p className="absolute bottom-2 right-4 text-xs text-gray-500">
        Hosted and managed by RamNaidu
      </p>
    </motion.div>
  );
}

export default WelcomePage;





