// src/MealPlanChanges.js
import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import {
  collection,
  query,
  where,
  orderBy,
  onSnapshot,
  doc,
  updateDoc,
  writeBatch,
} from "firebase/firestore";
import { useNavigate } from "react-router-dom";
import { getWeekId } from "./utils";
import Card from "./components/ui/Card";
import Button from "./components/ui/Button";

const MealPlanChanges = () => {
  const [changes, setChanges] = useState([]);
  const [clearing, setClearing] = useState(false);
  const weekId = getWeekId();
  const navigate = useNavigate();

  useEffect(() => {
    const q = query(
      collection(db, "mealPlanChanges"),
      where("weekId", "==", weekId),
      orderBy("timestamp", "desc")
    );

    const unsubscribe = onSnapshot(q, (querySnapshot) => {
      const changesData = [];
      querySnapshot.forEach((d) => changesData.push({ id: d.id, ...d.data() }));
      setChanges(changesData);
    });

    return () => unsubscribe();
  }, [weekId]);

  const acknowledgeChange = async (id) => {
    try {
      const docRef = doc(db, "mealPlanChanges", id);
      await updateDoc(docRef, { acknowledged: true });
      // optimistic UI update
      setChanges((prev) =>
        prev.map((c) => (c.id === id ? { ...c, acknowledged: true } : c))
      );
    } catch (err) {
      alert("Failed to acknowledge notification: " + err.message);
    }
  };

  const clearOldNotifications = async () => {
    if (
      !window.confirm(
        "Are you sure you want to clear all acknowledged notifications?"
      )
    )
      return;
    setClearing(true);

    try {
      const batch = writeBatch(db);
      changes
        .filter((c) => c.acknowledged)
        .forEach((c) => {
          const docRef = doc(db, "mealPlanChanges", c.id);
          batch.delete(docRef);
        });
      await batch.commit();
    } catch (err) {
      alert("Failed to clear notifications: " + err.message);
    } finally {
      setClearing(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-yellow-50 to-orange-100 flex flex-col items-center p-6">
      <Card className="w-full max-w-4xl p-8">
        <h2 className="text-3xl font-extrabold text-gray-800 mb-6 text-center">
          Meal Plan Changes (Week: {weekId}) 
        </h2>

        {/* Actions */}
        <div className="flex flex-col md:flex-row justify-center gap-4 mb-6">
          <Button onClick={() => navigate(-1)} variant="primary">
            Back
          </Button>
          <Button
            onClick={clearOldNotifications}
            variant="danger"
            disabled={clearing}
          >
            Clear Acknowledged Notifications
          </Button>
        </div>

        {changes.length === 0 ? (
          <p className="text-gray-600 text-center">No notifications</p>
        ) : (
          <ul className="space-y-4">
            {changes.map((change) => {
              const isAcknowledged =
                change.acknowledged === true || change.acknowledged === "true";
              const date =
                change.timestamp && change.timestamp.toDate
                  ? change.timestamp.toDate()
                  : new Date();
              const dateStr = date.toLocaleDateString("en-US", {
                month: "short",
                day: "numeric",
                year: "numeric",
              });
              const timeStr = date.toLocaleTimeString("en-US", {
                hour: "2-digit",
                minute: "2-digit",
                hour12: true,
              });

              return (
                <li
                  key={change.id}
                  className={`p-4 rounded-lg shadow-md border transition-all duration-300 ${
                    isAcknowledged
                      ? "bg-gray-100 text-gray-500"
                      : "bg-white hover:shadow-lg"
                  }`}
                >
                  <div className="font-semibold text-gray-800">
                    {change.user}
                  </div>
                  <div className="text-sm text-gray-600">
                    {change.message || `${change.user} changed ${change.day}`}
                  </div>
                  <div className="text-xs text-gray-400 mt-1">
                    {dateStr} {timeStr}
                  </div>
                  <div className="mt-2">
                    {!isAcknowledged && (
                      <Button
                        onClick={() => acknowledgeChange(change.id)}
                        variant="primary"
                        className="text-sm px-3 py-1"
                      >
                        Acknowledge 
                      </Button>
                    )}
                    {isAcknowledged && (
                      <span className="text-green-600 text-sm font-medium">
                        Acknowledged
                      </span>
                    )}
                  </div>
                </li>
              );
            })}
          </ul>
        )}
      </Card>
    </div>
  );
};

export default MealPlanChanges;














