// src/SummaryPage.js
import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import { doc, onSnapshot } from "firebase/firestore";
import { useUser } from "./UserContext";
import { useNavigate } from "react-router-dom";
import { days, getWeekDates, getWeekId } from "./utils";
import Card from "./components/ui/Card";
import Button from "./components/ui/Button";

const SummaryPage = () => {
  const [mealData, setMealData] = useState({});
  const { user, logout } = useUser(); //  include logout
  const navigate = useNavigate();

  const weekId = getWeekId();
  const dates = getWeekDates();

  useEffect(() => {
    const docRef = doc(db, "weeks", weekId);
    const unsubscribe = onSnapshot(docRef, (docSnap) => {
      if (docSnap.exists()) {
        setMealData(docSnap.data().users || {});
      }
    });
    return () => unsubscribe();
  }, [weekId]);

  const userMeals = mealData[user] || {};

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 to-pink-100 flex flex-col items-center p-6">
      <Card className="w-full max-w-5xl p-8 animate-fade-in">
        <h2 className="text-3xl font-extrabold text-gray-800 mb-6 text-center">
          {user}'s Weekly Summary 
        </h2>

        {/* Meals Summary */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {days.map((day, i) => {
            const meals = userMeals[day] || {
              lunch: false,
              dinner: false,
              neither: false,
            };

            const dateLabel = dates[i].toLocaleDateString("en-US", {
              month: "short",
              day: "numeric",
            });

            return (
              <Card
                key={day}
                className="p-6 rounded-xl shadow-lg border bg-gradient-to-br from-blue-50 to-purple-50 hover:shadow-2xl transition-all duration-300"
              >
                <h3 className="text-xl font-semibold text-gray-800 mb-2">
                  {day} <span className="text-gray-500">({dateLabel})</span>
                </h3>
                <ul className="space-y-2">
                  <li
                    className={`px-3 py-2 rounded-lg ${
                      meals.lunch
                        ? "bg-green-100 text-green-700 font-medium"
                        : "bg-gray-100 text-gray-500"
                    }`}
                  >
                    Lunch
                  </li>
                  <li
                    className={`px-3 py-2 rounded-lg ${
                      meals.dinner
                        ? "bg-green-100 text-green-700 font-medium"
                        : "bg-gray-100 text-gray-500"
                    }`}
                  >
                    Dinner
                  </li>
                  <li
                    className={`px-3 py-2 rounded-lg ${
                      meals.neither
                        ? "bg-red-100 text-red-600 font-medium"
                        : "bg-gray-100 text-gray-500"
                    }`}
                  >
                    Neither
                  </li>
                </ul>
              </Card>
            );
          })}
        </div>

        {/* Actions */}
        <div className="flex flex-col md:flex-row justify-center gap-4 mt-10">
          <Button
            onClick={() => navigate("/tracker")}
            variant="primary"
            className="px-6 py-3 bg-gradient-to-r from-green-500 to-emerald-600"
          >
            Edit My Meals
          </Button>
          <Button
            onClick={logout}
            variant="danger"
            className="px-6 py-3"
          >
            Log Out
          </Button>
        </div>
      </Card>
    </div>
  );
};

export default SummaryPage;










