import React from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";

import WelcomePage from "./WelcomePage";
import MealTracker from "./MealTracker";
import SummaryPage from "./SummaryPage";
import AdminView from "./AdminView";
import MealPlanChanges from "./MealPlanChanges";

import { useUser } from "./UserContext"; // <-- useUser hook here

function App() {
  const { user } = useUser(); // <-- get current user

  // Animation variants for page transitions
  const pageVariants = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
  };

  //  ProtectedRoute wrapper
  const ProtectedRoute = ({ children }) => {
    if (!user) {
      return <Navigate to="/" replace />;
    }
    return children;
  };

  return (
    <div
      className={`min-h-screen flex flex-col ${
        user === "Komathi" ? "theme-red" : "theme-default"
      }`}
    >
      <AnimatePresence mode="wait">
        <Routes>
          {/* Public Welcome Page */}
          <Route
            path="/"
            element={
              <motion.div
                key="welcome"
                initial="initial"
                animate="animate"
                exit="exit"
                variants={pageVariants}
                transition={{ duration: 0.4, ease: "easeInOut" }}
                className="flex-1 flex items-center justify-center"
              >
                <WelcomePage />
              </motion.div>
            }
          />

          {/* Redirect /LunchLogger to / */}
          <Route path="/LunchLogger" element={<Navigate to="/" replace />} />

          {/* Protected Routes */}
          <Route
            path="/tracker"
            element={
              <ProtectedRoute>
                <motion.div
                  key="tracker"
                  initial="initial"
                  animate="animate"
                  exit="exit"
                  variants={pageVariants}
                  transition={{ duration: 0.4, ease: "easeInOut" }}
                  className="flex-1 p-6"
                >
                  <MealTracker />
                </motion.div>
              </ProtectedRoute>
            }
          />
          <Route
            path="/summary"
            element={
              <ProtectedRoute>
                <motion.div
                  key="summary"
                  initial="initial"
                  animate="animate"
                  exit="exit"
                  variants={pageVariants}
                  transition={{ duration: 0.4, ease: "easeInOut" }}
                  className="flex-1 p-6"
                >
                  <SummaryPage />
                </motion.div>
              </ProtectedRoute>
            }
          />
          <Route
            path="/admin"
            element={
              <ProtectedRoute>
                <motion.div
                  key="admin"
                  initial="initial"
                  animate="animate"
                  exit="exit"
                  variants={pageVariants}
                  transition={{ duration: 0.4, ease: "easeInOut" }}
                  className="flex-1 p-6"
                >
                  <AdminView />
                </motion.div>
              </ProtectedRoute>
            }
          />
          <Route
            path="/changes"
            element={
              <ProtectedRoute>
                <motion.div
                  key="changes"
                  initial="initial"
                  animate="animate"
                  exit="exit"
                  variants={pageVariants}
                  transition={{ duration: 0.4, ease: "easeInOut" }}
                  className="flex-1 p-6"
                >
                  <MealPlanChanges />
                </motion.div>
              </ProtectedRoute>
            }
          />

          {/* Catch-all: redirect unknown paths */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </AnimatePresence>
    </div>
  );
}

export default App;




