// src/MealTrackerFinal.js
import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import {
  doc,
  setDoc,
  updateDoc,
  serverTimestamp,
  onSnapshot,
  getDoc,
  collection,
  query,
  where,
  getDocs,
  addDoc,
} from "firebase/firestore";
import { useUser } from "./UserContext";
import { useNavigate } from "react-router-dom";

import { days, getWeekDates, getWeekId, CountdownToReset } from "./utils";
import Card from "./components/ui/Card";
import Button from "./components/ui/Button";

const users = ["Ram", "Vaishu", "Suppiah", "Komathi"];

const pronouns = {
  Ram: { possessive: "his" },
  Vaishu: { possessive: "her" },
  Suppiah: { possessive: "his" },
  Komathi: { possessive: "her" },
  default: { possessive: "their" },
};

const MealTrackerFinal = () => {
  const [mealData, setMealData] = useState({});
  const [error, setError] = useState("");
  const [showNotification, setShowNotification] = useState(false);

  const weekId = getWeekId();
  const dates = getWeekDates();
  const { user, logout } = useUser();
  const navigate = useNavigate();

  useEffect(() => {
    const docRef = doc(db, "weeks", weekId);

    const unsubscribe = onSnapshot(docRef, async (docSnap) => {
      if (docSnap.exists()) {
        setMealData(docSnap.data().users || {});
      } else {
        const initialData = {};
        users.forEach((u) => {
          initialData[u] = {};
          days.forEach((day) => {
            initialData[u][day] = {
              lunch: false,
              dinner: false,
              neither: false,
            };
          });
        });
        await setDoc(docRef, {
          users: initialData,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp(),
        });
        setMealData(initialData);
      }
    });

    return () => unsubscribe();
  }, [weekId]);

  const handleCheckboxChange = (userName, day, mealType) => {
    if (user === "Komathi") return; //  Prevent Komathi from editing

    setError("");
    const currentData = mealData[userName]?.[day] || {
      lunch: false,
      dinner: false,
      neither: false,
    };

    let updatedDayData = { ...currentData };

    if (mealType === "neither") {
      updatedDayData.neither = !currentData.neither;
      if (updatedDayData.neither) {
        updatedDayData.lunch = false;
        updatedDayData.dinner = false;
      }
    } else {
      updatedDayData[mealType] = !currentData[mealType];
      if (updatedDayData[mealType]) {
        updatedDayData.neither = false;
      }
    }

    setMealData((prev) => ({
      ...prev,
      [userName]: {
        ...prev[userName],
        [day]: updatedDayData,
      },
    }));
  };

  const handleSubmit = async () => {
    if (user === "Komathi") {
      navigate("/summary"); //  Komathi goes straight to summary, no submit
      return;
    }

    setError("");

    for (const day of days) {
      const dayData = mealData[user]?.[day] || {};
      if (!dayData.lunch && !dayData.dinner && !dayData.neither) {
        setError(`Please select at least one meal or 'Neither' for ${day}.`);
        return;
      }
    }

    try {
      const docRef = doc(db, "weeks", weekId);
      const docSnap = await getDoc(docRef);
      const oldUsersMap = docSnap.exists() ? docSnap.data().users || {} : {};

      const oldUserData = oldUsersMap[user] || {};
      let hadAnySelectionBefore = false;
      for (const d of days) {
        const od = oldUserData[d];
        if (od && (od.lunch || od.dinner || od.neither)) {
          hadAnySelectionBefore = true;
          break;
        }
      }

      if (docSnap.exists()) {
        await updateDoc(docRef, {
          users: mealData,
          updatedAt: serverTimestamp(),
        });
      } else {
        await setDoc(docRef, {
          users: mealData,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp(),
        });
      }

      if (hadAnySelectionBefore) {
        for (const day of days) {
          const oldDay =
            oldUserData[day] || { lunch: false, dinner: false, neither: false };
          const newDay =
            mealData[user]?.[day] || { lunch: false, dinner: false, neither: false };

          const changed =
            oldDay.lunch !== newDay.lunch ||
            oldDay.dinner !== newDay.dinner ||
            oldDay.neither !== newDay.neither;

          if (changed) {
            const changesCollection = collection(db, "mealPlanChanges");
            const q = query(
              changesCollection,
              where("user", "==", user),
              where("day", "==", day),
              where("weekId", "==", weekId)
            );
            const querySnapshot = await getDocs(q);

            if (querySnapshot.empty) {
              const dateObj = dates[days.indexOf(day)];
              const dateStr = dateObj.toLocaleDateString("en-US", {
                month: "short",
                day: "numeric",
              });

              const pronoun = pronouns[user] || pronouns.default;
              const message = `${user} has changed ${pronoun.possessive} meal plan for ${day} (${dateStr}), please take note!`;

              await addDoc(changesCollection, {
                user,
                day,
                weekId,
                message,
                timestamp: serverTimestamp(),
                acknowledged: false,
              });
            }
          }
        }
      }

      setShowNotification(true);
    } catch (err) {
      console.error(err);
      setError("Failed to save changes. Please try again.");
    }
  };

  const hasUserSubmitted = (userName) => {
    const data = mealData[userName] || {};
    return days.some((d) => {
      const dd = data[d];
      return dd && (dd.lunch || dd.dinner || dd.neither);
    });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 to-pink-100 flex justify-center items-start p-6">
      <Card className="w-full max-w-6xl p-6">
        <h2 className="text-2xl font-bold mb-4 text-gray-900">
          Welcome, {user}! 
        </h2>

        <table
          border="1"
          cellPadding="8"
          style={{ borderCollapse: "collapse", marginTop: "10px" }}
          className="w-full mt-6"
        >
          <thead className="bg-gray-300 text-gray-900 sticky top-0">
            <tr>
              <th className="p-2">User</th>
              {dates.map((date, i) => (
                <th key={i} className="p-2">
                  {days[i]}
                  <br />
                  <small>
                    ({date.toLocaleDateString("en-US", {
                      month: "short",
                      day: "numeric",
                    })})
                  </small>
                </th>
              ))}
              {user === "Komathi" && <th className="p-2">Submitted?</th>}
            </tr>
          </thead>
          <tbody>
            {users
              // Komathi removed globally
              .filter((u) => u !== "Komathi")
              .map((userName) => (
                <tr key={userName}>
                  <td className="font-semibold bg-white text-gray-800 p-2">
                    {userName}
                  </td>
                  {days.map((day) => {
                    const dayData = mealData?.[userName]?.[day] || {
                      lunch: false,
                      dinner: false,
                      neither: false,
                    };
                    const disabled = userName !== user || user === "Komathi";

                    return (
                      <td key={day} className="p-2 bg-white">
                        <label className="flex items-center space-x-1 text-gray-800">
                          <input
                            type="checkbox"
                            checked={dayData.lunch}
                            disabled={disabled || dayData.neither}
                            onChange={() =>
                              handleCheckboxChange(userName, day, "lunch")
                            }
                          />
                          <span>Lunch</span>
                        </label>
                        <label className="flex items-center space-x-1 text-gray-800">
                          <input
                            type="checkbox"
                            checked={dayData.dinner}
                            disabled={disabled || dayData.neither}
                            onChange={() =>
                              handleCheckboxChange(userName, day, "dinner")
                            }
                          />
                          <span>Dinner</span>
                        </label>
                        <label className="flex items-center space-x-1 text-gray-800">
                          <input
                            type="checkbox"
                            checked={dayData.neither}
                            disabled={disabled}
                            onChange={() =>
                              handleCheckboxChange(userName, day, "neither")
                            }
                          />
                          <span>Neither</span>
                        </label>
                      </td>
                    );
                  })}
                  {user === "Komathi" && (
                    <td className="text-center bg-white">
                      {hasUserSubmitted(userName)
                        ? " Submitted"
                        : " Not Submitted"}
                    </td>
                  )}
                </tr>
              ))}
          </tbody>
        </table>

        {user !== "Komathi" ? (
          <Button onClick={handleSubmit} className="mt-4">
            Submit Changes and View Summary
          </Button>
        ) : (
          <div className="flex gap-4 mt-4">
            <Button onClick={logout}>Log out</Button>
            <Button onClick={() => navigate("/changes")}>
              View Notifications
            </Button>
          </div>
        )}

        {error && <div className="text-red-600 mt-4">{error}</div>}

        {showNotification && user !== "Komathi" && (
          <div className="bg-green-100 p-4 mt-4 rounded-lg">
            <p>Changes saved successfully! </p>
            <Button
              onClick={() => {
                setShowNotification(false);
                navigate("/summary");
              }}
              className="mt-2"
            >
              Go to Summary
            </Button>
          </div>
        )}

        <div className="mt-6">
          <CountdownToReset />
        </div>
      </Card>
    </div>
  );
};

export default MealTrackerFinal;




























