// src/AdminView.js
import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import { doc, onSnapshot } from "firebase/firestore";
import { days, getWeekId, getWeekDates } from "./utils";
import Card from "./components/ui/Card";
import Button from "./components/ui/Button";

const AdminView = () => {
  const [mealData, setMealData] = useState({});
  const weekId = getWeekId();
  const dates = getWeekDates();

  useEffect(() => {
    const docRef = doc(db, "weeks", weekId);
    const unsubscribe = onSnapshot(docRef, (docSnap) => {
      if (docSnap.exists()) {
        setMealData(docSnap.data().users || {});
      }
    });
    return () => unsubscribe();
  }, [weekId]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 flex flex-col items-center p-6">
      <Card className="w-full max-w-6xl p-8">
        <h2 className="text-3xl font-extrabold text-gray-800 mb-6 text-center">
          Admin View 
        </h2>

        {/* User meal summary table */}
        <div className="overflow-x-auto">
          <table className="min-w-full border-collapse">
            <thead>
              <tr>
                <th className="px-4 py-2 border text-left">User</th>
                {days.map((day, i) => {
                  const dateLabel = dates[i].toLocaleDateString("en-US", {
                    month: "short",
                    day: "numeric",
                  });
                  return (
                    <th key={day} className="px-4 py-2 border text-center">
                      {day} <span className="text-gray-500">({dateLabel})</span>
                    </th>
                  );
                })}
              </tr>
            </thead>
            <tbody>
              {Object.entries(mealData).map(([username, userMeals]) => (
                <tr key={username} className="hover:bg-gray-50">
                  <td className="px-4 py-2 border font-semibold">{username}</td>
                  {days.map((day) => {
                    const meals = userMeals[day] || {};
                    return (
                      <td
                        key={day}
                        className="px-4 py-2 border text-center space-y-1"
                      >
                        {meals.lunch && (
                          <span className="block bg-green-100 text-green-700 px-2 py-1 rounded-lg text-sm">
                            Lunch
                          </span>
                        )}
                        {meals.dinner && (
                          <span className="block bg-green-100 text-green-700 px-2 py-1 rounded-lg text-sm">
                            Dinner
                          </span>
                        )}
                        {meals.neither && (
                          <span className="block bg-red-100 text-red-600 px-2 py-1 rounded-lg text-sm">
                            Neither
                          </span>
                        )}
                        {!meals.lunch &&
                          !meals.dinner &&
                          !meals.neither && (
                            <span className="block text-gray-400 text-sm">
                              
                            </span>
                          )}
                      </td>
                    );
                  })}
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Actions */}
        <div className="flex justify-center mt-10">
          <Button
            onClick={() => window.history.back()}
            variant="danger"
            className="px-6 py-3"
          >
            Back
          </Button>
        </div>
      </Card>
    </div>
  );
};

export default AdminView;







